import { ExtensionsListSearchBox } from "./ExtensionSearchBox";

const av = Autodesk.Viewing;
const SettingsPanel = av.UI.SettingsPanel;
const TAB_ID = "extensionsTab";
const TAB_LABEL = "Extensions";

/**
 * @typedef {Object} registeredExtensions
 * @property {string} id
 * @property {boolean} inMemory
 * @property {boolean} insync
 */

export class ExtensionListPanel extends Autodesk.Viewing.UI.SettingsPanel {
    constructor(viewer, viewerContainer, options) {
        const PANEL_ID = "extension-list-panel";
        const PANEL_TITLE = Autodesk.Viewing.i18n.translate("Viewer Extensions List");
        super(viewerContainer, PANEL_ID, PANEL_TITLE, options);

        this.container.classList.add("extensions-panel");

        // Panel Settings
        this.addTab(TAB_ID, TAB_LABEL, { className: "extensions" });
        this.selectTab(TAB_ID);

        this.viewer = viewer;
        this.setGlobalManager(viewer.globalManager);

        this.getRegisteredExtensions = this.getRegisteredExtensions.bind(this);
        this.createExtensionsTableUI = this.createExtensionsTableUI.bind(this);
        this.addCheckbox = this.addCheckbox.bind(this);

        this.createExtensionsTableUI();

        this.searchbox = new ExtensionsListSearchBox("ExtensionSearch", {
            searchFunction: this.filterExtensions.bind(this),
            displayAll: this.displayAll.bind(this),
        });
        this.searchbox.setGlobalManager(this.globalManager);
        this.container.appendChild(this.searchbox.container);
    }

    displayAll() {
        const tableContainer = this.tablesContainer.firstChild;
        const extRows = tableContainer.getElementsByTagName("tr");
        extRows.forEach((e) => (e.style.display = ""));
    }

    filterExtensions(text) {
        const searchTerm = text.toLowerCase();

        const tableContainer = this.tablesContainer.firstChild;
        const extRows = tableContainer.getElementsByTagName("tr");

        const found = [];
        extRows.forEach(function (extnRow) {
            if (extnRow.innerText.toLowerCase().indexOf(searchTerm) !== -1) {
                found.push(extnRow.innerText);
                extnRow.style.display = "";
            } else {
                extnRow.style.display = "none";
            }
        });

        return found;
    }

    getRegisteredExtensions() {
        /** @type registeredExtensions[] */
        let registeredExtns = av.theExtensionManager.getRegisteredExtensions();
        // extract ids of registered extensions
        registeredExtns = registeredExtns.map(function (extensionData) {
            return extensionData.id;
        });

        return registeredExtns.sort();
    }

    createExtensionsTableUI() {
        const _this = this;
        const viewer = _this.viewer;
        _this.registeredExtns = this.getRegisteredExtensions();

        _this.registeredExtns.forEach((extObj) => {
            _this.addCheckbox(TAB_ID, extObj, !!viewer.getExtension(extObj), function (checked) {
                if (checked) {
                    const isLoaded = viewer.getExtension(extObj);
                    if (!isLoaded) {
                        viewer.loadExtension(extObj, viewer.config);
                    }
                } else {
                    viewer.unloadExtension(extObj);
                }
            });
        });

        _this.resizeToContent();
    }

    addCheckbox(tabId, name, initialState, onchange) {
        function onChangeCB(checked) {
            onchange && onchange(checked);
        }

        const checkboxId = SettingsPanel.prototype.addCheckbox.call(
            this,
            tabId,
            name,
            initialState,
            onChangeCB
        );

        return checkboxId;
    }
}
