import "./ExtensionsPanel.scss";
import { ExtensionListPanel } from "./ExtensionListPanel";
import { locales } from "./ExtensionsPanelLocales";
const av = Autodesk.Viewing;

class ExtensionsPanel extends Autodesk.Viewing.Extension {
    constructor(viewer, options) {
        super(viewer, options);
        this.panel = null;
    }

    load() {
        // Extend the localization strings
        this.extendLocalization(locales);
        
        if (this.viewer.toolbar) {
            // Toolbar is already available, create the UI
            this.createUI();
        } else {
            // Toolbar hasn't been created yet, wait until we get notification of its creation
            this.viewer.addEventListener(
                av.TOOLBAR_CREATED_EVENT,
                this._onToolbarCreated.bind(this)
            );
        }
        return true;
    }

    unload() {
        if (this.subToolbar) {
            this.viewer.toolbar.removeControl(this.subToolbar);
            this.subToolbar = null;
        }
    }

    createUI() {
        var panel = this.panel;

        // Panel already created, exit
        if (panel != null) {
            return;
        }

        // Create the Panel
        const options = { heightAdjustment: 144, width: 335, hideTabBar: true };
        this.panel = new ExtensionListPanel(this.viewer, this.viewer.container, options);

        // button to show the docking panel
        var showExtensionPanelButton = new av.UI.Button("ExtensionPanelBtn");
        showExtensionPanelButton.onClick = function (e) {
            this.panel.setVisible(!this.panel.isVisible());
        }.bind(this);

        showExtensionPanelButton.setIcon('adsk-icon-extn-showhide-button')
        showExtensionPanelButton.addClass("extn-showhide-button");
        showExtensionPanelButton.setToolTip("Load/Unload Extension");

        // SubToolbar
        this.subToolbar = new av.UI.ControlGroup("ExtensionsPanelToolbar");
        this.subToolbar.addControl(showExtensionPanelButton);

        this.viewer.toolbar.addControl(this.subToolbar);

        // Deactivate Button when docking panel is closed
        this.panel.addVisibilityListener((visible) => {
            if (visible) {
                this.viewer.onPanelVisible(this.panel);
            }
            showExtensionPanelButton.setState(
                visible ? av.UI.Button.State.ACTIVE : av.UI.Button.State.INACTIVE
            );
        });
    }

    _onToolbarCreated() {
        this.viewer.removeEventListener(av.TOOLBAR_CREATED_EVENT, this._onToolbarCreated);
        this.createUI();
    }
}

Autodesk.Viewing.theExtensionManager.registerExtension("Autodesk.ExtensionsPanel", ExtensionsPanel);
